.section__stats {
   padding-top: 20px;
   padding-bottom: 83px;
}
.stats__item {
   padding-top: 53px;
   box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 25px 35px rgba(22, 23, 72, 0.05);
   border-radius: 8px;
}
.stats__item-title {
   font-style: normal;
   font-weight: 700;
   font-size: 56px;
   line-height: 62px;
   color: #1A2578;
   @media (max-width: 992px){
      font-size: 48px;
      line-height: 1.3;
   }
   @media (max-width: 768px){
      font-size: 34px;
   }
}
.stats__item-text {
   font-style: normal;
   font-weight: 400;
   font-size: 22px;
   line-height: 33px;
   color: #484C63;
   @media (max-width: 768px){
      font-size: 18px;
   }
}
.stats__item-img {
   margin-top: auto;
   img{
      max-width: 100%;
      width: 100%;
      height: auto;
   }
}