.section__pluses {
   padding-top: 65px;
   padding-bottom: 97px;
}
.pluses {
   margin-top: 96px;
}
.pluses__item {
   padding: 40px;
   background: #FFFFFF;
   box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 25px 35px rgba(22, 23, 72, 0.05);
   border-radius: 8px;
   @media (max-width: 1399.98px){
      padding: 30px;
   }
   @media (max-width: 992px){
      padding: 20px;
   }
   @media (max-width: 768px){
      text-align: center;
   }
}
.pluses__item-title {
   font-weight: 400;
   font-style: normal;
   font-size: 28px;
   line-height: 31px;
   color: #3A3A3C;
   @media (max-width: 992px){
      font-size: 24px;
   }
}
.pluses__item-text {
   font-style: normal;
   font-weight: 400;
   font-size: 16px;
   line-height: 27px;
   color: #484C63;
   margin-top: 32px;
}