.section__photos {
   background: linear-gradient(270.76deg, #E3E7F6 -55.5%, rgba(227, 231, 246, 0) 145.36%);
   padding: 58px 0;
}
.photos__item {
   @media (max-width: 1399.98px){
      img{
         max-width: 100%;
         width: 100%;
         height: auto;
      }
   }
   @media (max-width: 768px){
      img{
         max-width: 100%;
      }
   }
   &-text{
      max-width: 322px;
      @media (max-width: 768px){
         max-width: 100%;
         text-align: center;
         .section__title{
            max-width: 100%;
         }
      }
      .section__title{
         max-width: 150px;
      }
   }
}