.section__info {
   padding: 73px 0;
   background: linear-gradient(268.52deg, #E3E7F6 -52.27%, rgba(227, 231, 246, 0) 138.23%);
   &.section__info-products{
      background: #fff;
   }
   &.section__info-about{
      padding-bottom: 96px;
      background: transparent;
   }
}
.info__item {
   @media (max-width: 992px){
      text-align: center;
      .info__img{
         max-width: 450px;
         margin-top: 35px;
      }
   }
   .info__img{
      @media (max-width: 1120px){
         max-width: 100%;
         width: 100%;
         height: auto;
      }
   }
}
.info__btn{
   &.info__btn-reletive{
      position: relative;
      @media (max-width: 768px){
         margin-bottom: 50px;
      }
      @media (max-width: 480px){
         margin-bottom: 0;
      }
      .path{
         transition: all .3s ease;
      }
      &:hover{
         .path{
            fill: red;
            transition: all .3s ease;
         }
      }
      svg{
         position: absolute;
         right: -60%;
         bottom: -130%;
         @media (max-width: 480px){
            display: none;
         }
      }
   }
}