.section__products {
   padding: 106px 0;
}
.section__subtitle{
   margin-top: 16px;
}
.products {
   margin-top: 67px;
   @media (max-width: 1399.98px){
      margin-top: 37px;
   }
}
.products__item {
   margin-top: 30px;
   box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 25px 35px rgba(22, 23, 72, 0.08);
   border-radius: 8px;
   @media (max-width: 768px){
      margin-right: 0;
   }
}
.product__item-content {
   padding: 62px 62px 0 62px;
   @media (max-width: 1399.98px){
      padding: 50px 50px 0 50px;
   }
   @media (max-width: 768px){
      padding: 30px 30px 0 30px;
   }
}
.products__item-logo {
   @media (max-width: 992px){
      img{
         max-width: 150px;
      }
   }
   @media (max-width: 480px){
      display: flex;
      justify-content: center;
   }
}
.products__item-text {
   margin: 40px 0 32px;
   font-style: normal;
   font-weight: normal;
   font-size: 24px;
   line-height: 31px;
   color: #3A3A3C;
   @media (max-width: 1399.98px){
      margin: 30px 0 20px;
      font-size: 22px;
   }
   @media (max-width: 992px){
      font-size: 18px;
      line-height: 1.2;
      margin: 20px 0 10px;
   }
   @media (max-width: 480px){
      text-align: center;
   }
}
.products__item-btn {
   display: inline-flex;
   align-items: center;
   padding: 15px 24px;
   border: 1px solid #BF0830;
   border-radius: 6px;
   text-decoration: none;
   @media (max-width: 768px){
      padding: 12px 18px;
   }
   @media (max-width: 480px){
      margin: 0 auto;
      transform: translate(-50%, 0);
      left: 50%;
      position: relative;
   }
   span{
      font-size: 14px;
      letter-spacing: 0.005em;
      color: #BF0830;
      margin-left: 8px;
      transition: .3s all ease;
   }
   path{
      transition: .3s all ease;
   }
   &:hover{
      span{
         color: #fff;
         transition: .3s all ease;
      }
      path{
         stroke: #fff;
         transition: .3s all ease;
      }
   }
}
.product__item-footer {
   margin-top: auto;
   padding: 0 10px;
   img{
      max-width: 100%;
      width: 100%;
      height: auto;
   }
}