.section__recomendation {
   padding: 56px 0 60px;
}
.recomendation {
   background: #F7F8FF;
   box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 25px 35px rgba(22, 23, 72, 0.05);
   border-radius: 8px;
   padding: 35px 100px 35px 70px;
   @media (max-width: 992px){
      padding: 35px;
   }
   @media (max-width: 768px){
      padding: 20px;
   }
}
.recomendation__item {
   &-img img{
      @media (max-width: 480px){
         max-width: 75px;
      }
   }
}
.recomendation__item-rating--stars img{
   @media (max-width: 480px){
      width: 25px;
   }
}
.recomendation__item-title {
   font-style: normal;
   font-weight: 700;
   font-size: 36px;
   line-height: 48px;
   color: #1A2578;
   @media (max-width: 992px){
      font-size: 32px;
      line-height: 1.3;
   }
   @media (max-width: 768px){
      font-size: 24px;
   }
   @media (max-width: 480px){
      font-size: 20px;
   }
}
.recomendation__item-subtitle {
   @media (max-width: 768px){
      img{
         max-width: 150px;
         width: 100%;
      }
   }
   @media (max-width: 480px){
      img{
         max-width: 120px;
         width: 100%;
      }
   }
}
.recomendation__item-rating--num {
   font-style: normal;
   font-weight: 700;
   font-size: 36px;
   line-height: 62px;
   color: #000000;
   @media (max-width: 768px){
      font-size: 24px;
      line-height: 1.5;
   }
}
.recomendation__item-link {
   font-style: normal;
   font-weight: 700;
   font-size: 18px;
   line-height: 24px;
   text-decoration-line: underline;
   color: #424B5A;
   @media (max-width: 768px){
      font-size: 16px;
   }
   svg{
      margin-left: 7px;
      transition: all .3s ease;
   }
   &:hover{
      color:#424B5A;
      svg{
         transform: translate(5px, 0);
      }
   }
}