.header__nav-link {
	margin-right: 60px;
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 22px;
	transition: all 0.3s ease 0s;
	color: #484c63;
	&:hover {
		color: #bf0830 !important;
	}
	@media (max-width: 992px) {
		font-size: 16px;
		margin-right: 0px;
		margin-bottom: 20px;
	}
}
.navbar-toggler{
	color: #fff !important;
	border: none;
	.navbar-toggler-icon{
		position: relative;
		height: 20px;
		width: 30px;
		background: none !important;
		&::before, &::after{
			content: '';
			position: absolute;
			height: 2px;
			width: 100%;
			right: 0;
			background-color: #BF0830;
			transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) .3s all;
		}
		&::before{
			top: 0;
			transform: rotate(0deg);
		}
		&::after{
			bottom: 0;
			transform: rotate(0deg);
		}
		span{
			position: absolute;
			top: 9px;
			right: 0;
			width: 100%;
			background-color: #BF0830;
			height: 2px;
			transform: scale(1);
			transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) .3s all;
		}
	}
	&.active{
		.navbar-toggler-icon{
			position: relative;
			height: 20px;
			width: 30px;
			background: none !important;
			&::before, &::after{
				content: '';
				position: absolute;
				height: 2px;
				width: 100%;
				right: 0;
				background-color: #BF0830;
			}
			&::before{
				transform: rotate(45deg);
				transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) .3s all;
				top: 8px;
			}
			&::after{
				transform: rotate(-45deg);
				transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) .3s all;
				bottom: 10px;
			}
			& span{
				transform: scale(0);
				position: absolute;
				right: 0;
				width: 100%;
				background-color: #BF0830;
				height: 2px;
				transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) .3s all;
			}
		}
	}
}
.navbar{
	padding: 31px 0;
	@media (max-width: 992px){
		padding: 15px 0;
		&.active{
			width: 100%;
			height: 100vh;
			position: fixed;
			left: 0;
			top: 0;
			bottom: 0;
			right: 0;
			z-index: 999;
			background-color: #fff;
			.container{
				position: relative;
				height: 100%;
			}
			.navbar-brand{
				position: absolute;
				top: 0;
				left: 0;
			}
			.navbar-toggler{
				position: absolute;
				right: 0;
				top: 0;
			}
		}
	}
	@media (max-width: 576px){
		&.active{
			.navbar-brand{
				left: 10px;
			}
			.navbar-toggler{
				top: 8px;
				right: 10px;
			}
		}
	}
}
.nav{
	@media (max-width: 992px){
		display: none;
		&.active{
			display: flex;
		}
	}
}