.section__contacts {
   padding: 114px 0 138px;
}
.contacts__form {
   width: 100%;
   background: #FFFFFF;
   box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 25px 35px rgba(22, 23, 72, 0.05);
   border-radius: 8px;
}
.form-label {
   font-style: normal;
   font-weight: 400;
   font-size: 16px;
   line-height: 150%;
   letter-spacing: 0.005em;
   color: #8F90A6;
}
.form-control {
   background: #FFFFFF;
   border: 1px solid #E3E7F6;
   border-radius: 6px;
   font-style: normal;
   font-weight: 400;
   font-size: 16px;
   line-height: 150%;
   letter-spacing: 0.005em;
   color: #8F90A6;
   &:focus{
      border: 1px solid #E3E7F6 !important;
      color: #8F90A6 !important;
   } 
}
.form-textarea {
   resize: none;
   height: 148px;
   width: 100%;
   @media (max-width: 768px){
     height: 75px;
   }
}
.contacts__form-btn {
   border: none;
}
.contacts__places {
   max-width: 255px;
   @media (max-width: 992px){
      max-width: 100%;
   }
}
.contacts__place {
   margin-top: 72px;
   @media (max-width: 992px){
      max-width: 450px;
      margin: 30px auto 0;
   }
}
.contacts__place-title {
   font-style: normal;
   font-weight: 700;
   font-size: 18px;
   line-height: 150%;
   letter-spacing: 0.005em;
   color: #000000;
}
.contacts__place-location {
   font-style: normal;
   font-weight: 400;
   font-size: 16px;
   line-height: 150%;
   letter-spacing: 0.005em;
   color: #000000;
}