.section__jobs {
   padding: 60px 0;
}
.jobs {
   margin-top: 70px;
}
.jobs__item {
   box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 17px 35px rgba(22, 23, 72, 0.05);
   border-radius: 8px;
   padding: 35px 60px;
   @media (max-width: 768px){
      padding: 20px 30px;
   }
   @media (max-width: 480px){
      padding: 20px 10px;
   }
   .section__btn-outline{
      white-space: nowrap;
   }
}
.jobs__title {
   font-style: normal;
   font-weight: 400;
   font-size: 28px;
   line-height: 27px;
   color: #484C63;
   @media (max-width: 768px){
      font-size: 22px;
      line-height: 130%;
   }
}
.jobs__text {
   font-style: normal;
   font-weight: 400;
   font-size: 20px;
   line-height: 27px;
   color: #8F90A6;
}
.more__btn{
   margin-top: 68px;
   .jobs__btn{
      display: block;
   }
}