body {
	font-family: "Europa", sans-serif;
	overflow-x: hidden;
}
ul{
	list-style: none !important;
}
a{
	text-decoration: none !important;
}
@keyframes fade {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
.section__btn {
	background: #bf0830;
	border-radius: 6px;
	padding: 14px 32px;
	display: inline-block;
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 150%;
	transition: all 0.3s ease 0s;
	color: #fff;
	text-decoration: none;
	@media (max-width: 992px) {
		padding: 12px 25px;
	}
	&:hover {
		background: #a20a2b;
		color: #fff;
	}
}
.section__btn-outline {
	border-radius: 6px;
	padding: 14px 32px;
	display: inline-block;
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 150%;
	transition: all 0.3s ease 0s;
	color: #bf0830;
	border: 1px solid #bf0830;
	text-decoration: none;
	&:hover {
		background: #bf0830;
		color: #fff;
	}
}
.section {
	@media (max-width: 992px) {
		padding: 75px 0;
	}
	@media (max-width: 768px) {
		padding: 50px 0;
	}
	@media (max-width: 480px) {
		padding: 35px 0;
	}
}
.section__title {
	font-style: normal;
	font-weight: bold;
	font-size: 44px;
	line-height: 64px;
	color: #1a2578;
	&.title__center {
		text-align: center;
	}
	@media (max-width: 992px) {
		font-size: 32px;
		line-height: 50px;
	}
	@media (max-width: 768px) {
		font-size: 36px;
		line-height: 42px;
	}
	@media (max-width: 992px) {
		font-size: 28px;
		line-height: 34px;
	}
	span {
		color: #bf0830;
	}
}
.section__subtitle {
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 27px;
	color: #484c63;
	&.subtitle__center {
		text-align: center;
	}
}
.about__wrapper {
	background: linear-gradient(268.52deg, #e3e7f6 -52.27%, rgba(227, 231, 246, 0) 138.23%);
}
.jobs__btn {
	display: none;
}

