.section__innovation {
   padding: 120px 0 30px;
}
.swiper-container {
   margin-top: 70px;
   @media (max-width: 992px){
      margin-top: 30px;
   }
}
.swiper-wrapper {
   padding: 45px 0;
}
.swiper-slide {
   background: #FFFFFF;
   box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 25px 35px rgba(22, 23, 72, 0.05);
   border-radius: 8px;
   height: 119px;
}
.swiper__arrows {
   @media (max-width: 768px){
      display: none !important;
   }
}
.swiper__arrow {
   background: none;
   border: none;
   &.swiper-button-disabled{
      svg{
         opacity: 0.7;
      }
   }
   
}
.swiper__arrow-prev { 
   transform: rotate(-180deg);
   margin-right: 10px;
}