.hero__wrapper{
   background: linear-gradient(252.91deg, #E3E7F6 3.68%, rgba(227, 231, 246, 0) 97.82%);
}
.section__hero {
   padding: 129px 0 210px;
   position: relative;
   @media (max-width: 1399.98px){
      padding: 100px 0 !important;
   }
   @media (max-width: 992px){
      padding: 20px 0 !important ;
   }
   &.section__hero-products{
      padding: 170px 0 150px;
      .hero__item{
         @media (max-width: 1200px){
            max-width: 450px;
         }
         @media (max-width: 992px){
            text-align: center;
            margin: 0 auto;
            max-width: 100%;
         }
      }
      .hero__img{
         right: 120px;
         @media (max-width: 1399.98px){
            max-width: 500px;
            right: 0;
         }
      }
   }
   &.section__hero-about{
      padding: 189px 0 212px;
      .hero__img{
         right: 120px;
         @media (max-width: 1399.98px){
            max-width: 600px;
            right: 0;
            top: 0px;
         }
      }
      .hero__item{
         max-width: 400px;
         @media (max-width: 992px){
            max-width: 100%;
         }
      }
   }
   &.section__hero-career{
      padding: 189px 0 212px;
      .hero__img{
         right: 120px;
         @media (max-width: 1399.98px){
            max-width: 600px;
            right: 0;
         }
      }
      .hero__item{
         max-width: 400px;
         @media (max-width: 992px){
            max-width: 100%;
            margin: 0 auto;
         }
      }
      .hero__career-btn{
         @media (max-width: 992px){
            position: relative;
            left: 50%;
            transform: translate(-50%, 0);
         }  
      }
   } 
   picture{
      @media (max-width: 992px){
         display: flex;
         justify-content: center;
         align-items: center;
      }
   }
   .hero__img{
      position: absolute;
      bottom: 0;
      right: 75px;   
      @media (max-width: 1399.98px){
         max-width: 600px;
         width: 100%;
         right: 0;
      }
      @media (max-width: 992px){
         position: relative;
         max-width: 500px;
      }
      @media (max-width: 768px){
         max-width: 400px;
      }
      @media (max-width: 400px){
         max-width: 320px;
      }
   }
}
.hero__item-title {
   font-style: normal;
   font-weight: bold;
   font-size: 48px;
   line-height: 62px;
   color: #1A2578;
   span{
      color: #BF0830;
   }
   @media (max-width: 992px){
      font-size: 38px;
      line-height: 48px;
      text-align: center;
   }
   @media (max-width: 768px){
      font-size: 32px;
      line-height: 150%;
   }
}
.hero__item-text {
   font-style: normal;
   font-weight: normal;
   font-size: 22px;
   line-height: 33px;
   color: #484C63;
   @media (max-width: 992px){
      text-align: center;
   }
   @media (max-width: 768px){
      font-size: 18px;
      line-height: 24px;
   }
}
.hero__item{
   max-width: 570px;
   @media (max-width: 1120px){
      max-width: 500px;
   }
   @media (max-width: 992px){
      max-width: 100%;
   }
}