footer{
   background: #0D123C;
}
.footer {
   padding: 55px 0;
   @media (max-width: 768px){
      padding: 35px 0;
   }
}
.footer__text {
   font-style: normal;
   font-weight: 400;
   font-size: 16px;
   line-height: 27px;
   color: #FFFFFF;
   margin-top: 25px;
   max-width: 287px;
   @media (max-width: 768px){
      max-width: 100%;
      text-align: center;
   }
}
.footer__lists {
   max-width: 455px;
   margin-left: auto;
   margin-bottom: 30px;
   @media (max-width: 992px){
      margin-left: 0;
      max-width: 100%;
      margin-top: 25px;
   }
}
.footer__list-title {
   font-weight: bold;
   font-style: normal;
   font-size: 18px;
   line-height: 21px;
   color: #FFFFFF;
   @media (max-width: 576px){
      text-align: center;
   }
}
.footer__list-item--link {
   font-style: normal;
   font-weight: 400;
   font-size: 16px;
   line-height: 20px;
   color: #FFFFFF;
   transition: all .3s ease;
   @media (max-width: 576px){
      text-align: center;
   }
   &:hover{
      color: #BF0830;
      transition: all .3s ease;
   }
}
.footer__social-item--link {
   path{
      transition: all .3s ease;
   }
   &:hover{
      path{
         fill: #BF0830;
         transition: all .3s ease;
      }
   }
}
.footer__copyright {
   border-top: 1px solid rgba(197, 203, 226, 0.5);
}
.footer__copy-item {
   font-style: normal;
   font-weight: 400;
   font-size: 16px;
   line-height: 20px;
   color: #FFFFFF;
   @media (max-width: 480px){
      text-align: center;
   }
   a{
      text-decoration: underline;
      color: #FFFFFF;
   }
}